.main-banner {
  padding: 28px $mobile-margins 0;

  background-repeat: no-repeat;
  background-position: center top;
  background-size: $mobile-width 305px;

  background-image:
    url("../img/index-background-mobile@1x.jpg"),
    linear-gradient(to bottom, $green 305px, transparent 305px);

  @media (min-resolution: $retina-dppx) {
    background-image:
      url("../img/index-background-mobile@2x.jpg"),
      linear-gradient(to bottom, $green 305px, transparent 305px);
  }

  min-height: 480px;
}

.main-banner__title {
  @include clear-margin-padding;

  color: $white;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  text-align: center;
  max-width: 100%;
}

.main-banner__description {
  @include clear-margin-padding;
  margin-top: 27px;

  color: $white;
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  text-transform: uppercase;
  text-align: center;
  max-width: 100%;
}

.main-banner__img {
  display: block;
  margin-top: 28px;
  text-align: center;
  width: 280px;
  height: 271px;
}

.main-banner__button {
  display: block;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  margin-top: 3px;
  height: 40px;
}

@media (min-width: $tablet-width) {
  .main-banner {
    position: relative;
    align-items: flex-start;
    min-height: 868px;
    padding-left: 120px;
    padding-right: 120px;
    background-image: none;
  }

  .main-banner__title {
    margin-top: 30px;

    font-size: 60px;
    line-height: 60px;
    max-width: 100%;

    text-align: left;
    color: $verydarkgray;
  }

  .main-banner__description {
    margin-top: 42px;

    font-size: 20px;
    line-height: 20px;
    max-width: 100%;

    text-align: left;
    color: $verydarkgray;
  }

  .main-banner__img {
    position: absolute;
    left: 50%;
    top: 230px;
    width: 709px;
    height: 609px;
    transform: translateX(-45%);
    z-index: -1;

    @media (min-resolution: $retina-dppx) {
      width: 709px;
      height: 609px;
    }
  }

  .main-banner__button {
    min-height: 52px;
    width: 245px;
    margin-top: 48px;
    padding-top: 16px;

    font-size: 20px;
    line-height: 20px;
  }
}

@media (min-width: $desktop-width) {
  .main-banner {
    padding-left: 120px;
    padding-right: 50%;

    min-height: 583px;
    background-size: 720px 696px;
  }

  .main-banner__title {
    margin-top: 80px;
  }

  .main-banner__description {
    margin-top: 40px;
  }

  .main-banner__button {
    margin-top: 55px;
  }

  .main-banner__img {
    z-index: 1;
    top: 0;
    width: 552px;
    height: 499px;

    left: 50%;
    transform: translateX(-160px);
  }
}
