.cases {
  padding-top: 5px;
  background-color: $lightgray;
}

.cases__info {
  padding: 10px $mobile-margins 10px;
}

.cases__title {
  @include clear-margin-padding;
  color: $verydarkgray;
  font-size: 36px;
  font-weight: 400;
  line-height: 60px;
}

.cases__list {
  @include clear-ul-format;
  margin: 0;
}

.cases__description {
  @include clear-margin-padding;
  margin-top: 23px;

  color: $gray;
  font-family: $font-family-secondary;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

.cases__result-list {
  @include clear-margin-padding;
  margin-top: 25px;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.cases__result-item {
  position: relative;
  width: 125px;
  padding-top: 13px;

  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;

  border: 1px solid $lightgray2;
  border-radius: 3px;

  &--cost {
    @include clear-margin-padding;
    margin-top: 35px;

    flex-grow: 1;
    flex-direction: row;
    text-align: center;

    border: none;
    width: auto;
  }
}

.cases__result-name {
  position: relative;
  margin: 0;
  margin-top: -7px;
  padding: 0 5px;
  color: $gray;
  font-family: $font-family-secondary;
  font-size: 12px;
  font-weight: 400;
  line-height: 12px;
  text-transform: lowercase;

  top: 16px;
  max-width: 87px;

  background-color: $lightgray;

  order: 1;

  &--cost {
    @include clear-margin-padding;
    position: static;
    display: inline;
    order: 0;

    width: auto;
    max-width: none;
    margin-left: auto;

    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    font-family: $font-family-base;
    text-transform: uppercase;

    background-color: transparent;
  }
}

.cases__result-value {
  margin: 0;
  padding: 0;
  color: $verydarkgray;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  text-transform: uppercase;

  &--cost {
    @include clear-margin-padding;

    margin-right: auto;

    display: inline;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    font-family: $font-family-base;
  }
}

.cases__img {
  width: 320px;
  height: 240px;
  max-width: none;
}

.cases__widget {
  margin-top: 23px;
  width: 320px;
  padding-bottom: 33px;
  text-align: center;

  &--show-before {
    & .cases__toggle-indicator {
      left: 5px;
    }

    & .cases__widget-frame {
      &--before {
        width: 100%;
      }

      &--after {
        width: 0;
      }
    }
  }

  &--show-after {
    & .cases__toggle-indicator {
      right: 5px;
    }

    & .cases__widget-frame {
      &--before {
        display: none;
      }

      &--after {
        width: 100%;
      }
    }
  }

  &--show-middle {
    & .cases__toggle-indicator {
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }

    & .cases__widget-frame {
      &--before {
        width: 160px;
      }

      &--after {
        width: 160px;
      }
    }

    & .cases__img-after {
      margin-left: -100%;
    }
  }
}

.cases__img-wrapper {
  width: 320px;
  height: 240px;
  display: flex;
}

.cases__widget-frame {
  position: relative;
  overflow: hidden;
  height: 240px;
  width: 50%;
}

.cases__toggle {
  margin: 0 auto;
  margin-top: 30px;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  bottom: 33px;
  width: 210px;
}

.cases__toggle-range {
  position: relative;
  width: 84px;
  height: 14px;
  background-color: $white;
  border-radius: 6px;
  border: 1px solid $lightgray6;
}

.cases__toggle-indicator {
  position: absolute;

  top: 50%;
  transform: translateY(-50%);

  width: 35px;
  height: 6px;
  border-radius: 3px;
  background-color: $green;
}

.cases__toggle-button {
  color: $verydarkgray;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  border: none;
  box-shadow: none;
  background-color: inherit;
  cursor: pointer;
}

@media (min-width: $tablet-width) {
  .cases {
    position: relative;
    padding: 30px $tablet-margins 0;
    background-color: inherit;
  }

  .cases__info {
    padding: 15px $tablet-margins 10px;
  }

  .cases__title {
    font-size: 60px;
    margin-top: 15px;
  }

  .cases__description {
    margin-top: 63px;
    font-size: 16px;
    line-height: 24px;

    width: 680px;
  }

  .cases__result-list {
    margin-top: 52px;
  }

  .cases__result-item {
    width: 162px;
    padding-top: 15px;

    &--cost {
      @include clear-margin-padding;
      display: block;
      align-self: center;
      text-align: left;

      margin-left: auto;

      font-family: $font-family-base;
    }

    & + & {
      margin-left: 42px;
    }
  }

  .cases__result-value {
    display: block;
    font-size: 30px;
    line-height: 37px;

    &--cost {
      @include clear-margin-padding;
      font-size: 20px;
      line-height: 20px;

      text-align: right;

      margin-top: 10px;
    }
  }

  .cases__result-name {
    display: block;
    background-color: $white;
    max-width: 145px;
    top: 6px;

    font-size: 14px;
    line-height: 14px;

    margin-top: 4px;

    &--cost {
      @include clear-margin-padding;
      font-size: 20px;
      line-height: 20px;
      text-align: right;
      max-width: none;
    }
  }

  .cases__widget {
    margin: 0 auto;
    margin-top: 75px;
    width: 690px;
    padding-bottom: 74px;

    &--show-before {
      & .cases__widget-frame {
        &--before {
          width: 100%;
        }

        &--after {
          width: 0;
        }
      }

      & .cases__img-wrapper {
        &::before {
          background-image: linear-gradient(to right, $lightgray 100vw, $lightgray8 100vw);
        }
      }
    }

    &--show-after {
      & .cases__toggle-indicator {
        right: 5px;
      }

      & .cases__widget-frame {
        &--before {
          display: none;
        }

        &--after {
          width: 100%;
        }
      }

      & .cases__img-wrapper {
        &::before {
          background-image: linear-gradient(to right, $lightgray 0, $lightgray8 0);
        }
      }
    }

    &--show-middle {
      & .cases__toggle-indicator {
        left: 50%;
      }

      & .cases__widget-frame {
        &--before {
          width: 50%;
        }

        &--after {
          width: 50%;
        }
      }

      & .cases__img-after {
        margin-left: -100%;
      }
    }
  }

  .cases__img {
    width: 690px;
    height: 517px;
  }

  .cases__img-wrapper {
    width: 690px;
    height: 517px;

    &::before {
      content: "";
      position: absolute;

      background-image: linear-gradient(to right, $lightgray 50vw, $lightgray8 50vw);
      width: 99vw;
      left: calc(50% - 50vw);
      z-index: -100;
      bottom: 5px;
      height: 462px;
    }
  }

  .cases__widget-frame {
    height: 517px;
    width: 50%;
  }

  .cases__toggle {
    margin-top: 20px;
    bottom: 73px;
    width: 84%;
  }

  .cases__toggle-range {
    height: 6px;
    border-radius: 3px;
    background-color: $lightgray6;
    flex-grow: 1;
    margin-left: 10px;
    margin-right: 10px;
  }

  .cases__toggle-indicator {
    width: 34px;
    height: 34px;

    box-shadow: 0 2px 5px $toggle-shadow-color;
    border: 1px solid $lightgray6;
    background-color: $white;
    border-radius: 17px;

    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      left: 12px;
      top: 12px;
      width: 8px;
      height: 8px;
      border-radius: 4px;
      background-color: $green;
    }

    &:hover {
      box-shadow: 0 3px 5px 0 $toggle-shadow-hover-color;
    }

    &:active,
    &:focus {
      outline: none;
      box-shadow:
        0 0 0 1px $green,
        0 5px 6px 0 $toggle-shadow-active-color;
      border: 1px solid $green;
    }
  }

  .cases__toggle-button {
    @include clear-margin-padding;
    font-size: 20px;

    padding-bottom: 6px;
    border-bottom: 1px dashed rgba(17, 17, 17, 0.23);

    &:hover {
      border-bottom: 1px solid rgba(17, 17, 17, 0.4);
    }

    &:active,
    &:focus {
      border-bottom: none;
    }
  }
}

@media (min-width: $desktop-width) {
  .cases {
    padding: 0;
    padding-top: 10px;
    padding-bottom: 75px;
  }

  .cases__item {
    padding-left: $desktop-margins;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
  }

  .cases__info {
    @include clear-margin-padding;
    padding-top: 59px;
    width: 450px;
  }

  .cases__widget {
    top: 15px;
    left: 47vw;

    @include clear-margin-padding;
    padding-top: 0;
    margin-top: 5px;
    width: 690px;

    &--show-after {
      & .cases__img-wrapper {
        &::before {
          background-image: linear-gradient(to right, $lightgray 0, $lightgray8 0);
        }
      }
    }

    &--show-middle {
      & .cases__img-wrapper {
        &::before {
          background-image: linear-gradient(to right, $lightgray calc(50vw + 300px), $lightgray8 calc(50vw + 300px));
        }
      }
    }
  }

  .cases__title {
    margin-top: 72px;
  }

  .cases__description {
    width: 435px;
    margin-top: 145px;
  }

  .cases__result-list {
    width: 366px;
    margin-top: 70px;
  }

  .cases__result-item {
    @include clear-margin-padding;

    & + & {
      @include clear-margin-padding;
    }

    &--cost {
      margin-right: auto;

      width: 330px;
      flex-direction: row;
    }

    &:last-child {
      margin-top: 60px;
    }
  }

  .cases__result-name {
    background-color: $lightgray;
    margin-top: 7px;

    &--cost {
      @include clear-margin-padding;
      display: inline;
      text-align: left;

      max-width: none;
      font-size: 20px;
      line-height: 20px;
    }
  }

  .cases__result-value {
    margin-top: 14px;

    &--cost {
      @include clear-margin-padding;
      display: inline;

      text-align: left;

      font-size: 20px;
      line-height: 20px;

      margin-left: 64px;
    }
  }

  .cases__img-wrapper {
    &::before {
      content: "";
      position: absolute;
      z-index: -100;
      bottom: 0;
      height: 462px;
      width: 99vw;
    }
  }

  .cases__widget-frame {
    height: 517px;
    width: 50%;
  }

  .cases__toggle {
    margin-top: 40px;
    width: 583px;
  }

  .cases__toggle-range {
    height: 6px;
    border-radius: 3px;
    background-color: $lightgray6;
    flex-grow: 1;
    margin-left: 10px;
    margin-right: 10px;
  }

  .cases__toggle-indicator {
    width: 34px;
    height: 34px;

    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
    border: 1px solid $lightgray6;
    background-color: $white;
    border-radius: 17px;
  }

  .cases__toggle-button {
    font-size: 20px;
  }
}
